



































































import Vue from 'vue'
import { Player } from '@/store';

export default Vue.extend({
    name: 'leader-board',
    data() {
        return {
            deletePlayer: false,
            playerToDelete: null as Player | null
        }
    },
    watch: {
        deletePlayer(v) {
            this.$luigi()
            if (!v && this.playerToDelete) this.playerToDelete = null
        }
    },
    methods: {
        deletePlayerMaybe(player: Player) {
            this.deletePlayer = true
            this.playerToDelete = player
        },
        deleteThePlayer() {
            this.$store.commit('deletePlayer', this.playerToDelete)
            this.deletePlayer = false
        }
    },

})
