








































































































































import Vue from 'vue'

export default Vue.extend({
    name: 'games-list',
    data() {
        return {
            deleteGame: false,
            gameToDelete: null as number | null
        }
    },
    watch: {
        deleteGame(v) {
            this.$luigi()
            if (!v && this.gameToDelete) this.gameToDelete = null
        }
    },
    methods: {
        deleteGameMaybe(game: number) {
            this.deleteGame = true
            this.gameToDelete = game
        },
        deleteTheGame() {
            this.$store.commit('deleteGame', this.gameToDelete)
            this.deleteGame = false
        }
    },
})
