import Vue from 'vue'
import Vuex from 'vuex'
import characters, { Fighter } from '@/data/characters'

Vue.use(Vuex)

export interface Player {
    name: string,
    tag: string,
    mains: Fighter[]
}
export interface PlayerStats {
    name: string,
    tag: string,
    mains: Fighter[],
    wins: number,
    losses: number,
    twoStocks: number,
    comebacks: number,
    stylePoints: number,
    points: number,
    medal: string
}

export interface PlayerInstance {
    player: Player,
    fighter: Fighter,
    victory: boolean,
    twoStock?: boolean,
    comeback?: boolean,
    style?: number
}

export interface Game {
    players: [PlayerInstance, PlayerInstance],
    timestamp: number
}

function savePlayers(data: Player[]) {
    localStorage.setItem('players', JSON.stringify(data))
}
function saveGames(data: Game[]) {
    localStorage.setItem('games', JSON.stringify(data))
}

// localStorage.clear()

const players = localStorage.getItem('players')
const games = localStorage.getItem('games')


export default new Vuex.Store({
    state: {
        characters,
        players: players ? JSON.parse(players) : [] as Player[],
        games: games ? JSON.parse(games) : [] as Game[],
    },
    mutations: {
        deleteGame(state, index: number) {
            state.games.splice(index, 1)
            saveGames(state.games)
        },
        deletePlayer(state, player: PlayerStats) {
            const index = state.players.findIndex((c: Player) => player.tag === c.tag)
            state.players.splice(index, 1)
            savePlayers(state.players)
        },
        addNewPlayer(state, player: Player) {
            state.players.push(player)
            savePlayers(state.players)
        },
        addNewGame(state, game: Game) {
            state.games.unshift(game)
            saveGames(state.games)
        }
    },
    actions: {

    },
    getters: {
        rankedPlayers(state, getters) {

            const comebacks = getters.playerStats.sort((p1: PlayerStats, p2: PlayerStats) => {
                return (p2.comebacks || 0) - p1.comebacks || 0
            })
            if (comebacks[0] && comebacks[0].comebacks > 0) comebacks[0].medal = 'accessible_forward'

            const stylePoints = comebacks.sort((p1: PlayerStats, p2: PlayerStats) => {
                return (p2.stylePoints || 0) - (p1.stylePoints || 0)
            })
            if (stylePoints[0] && stylePoints[0].stylePoints > 0) stylePoints[0].medal = 'stars'

            const twoStocks = stylePoints.sort((p1: PlayerStats, p2: PlayerStats) => {
                return (p2.twoStocks || 0) - (p1.twoStocks || 0)
            })
            if (twoStocks[0] && twoStocks[0].twoStocks > 0) twoStocks[0].medal = 'whatshot'

            return twoStocks.sort((p1: PlayerStats, p2: PlayerStats) => {
                return (p2.points || 0) - (p1.points || 0)
            })
        },
        playerStats(state) {
            return state.players.map((player: Player) => {
                const winValue = 10
                const twoStocksValue = 5

                const stats = {
                    wins: 0,
                    losses: 0,
                    twoStocks: 0,
                    comebacks: 0,
                    stylePoints: 0
                }

                state.games.forEach((game: Game) => {
                    if (game.players) {
                        const meInstance = game.players.find(playerInstance => {
                            return playerInstance.player.tag === player.tag
                        })
                        if (meInstance) {
                            meInstance.victory ? stats.wins++ : stats.losses++
                            if (meInstance.twoStock) stats.twoStocks++
                            if (meInstance.comeback) stats.comebacks++
                            if (meInstance.style) stats.stylePoints += meInstance.style
                        }
                    }


                })
                const points = (stats.wins * winValue) + (stats.twoStocks * twoStocksValue) + stats.stylePoints


                return {
                    points,
                    ...stats,
                    ...player
                }
            })
        }
    },

})
