













































































































































































import Vue from 'vue'
import { PlayerInstance, Player, Game } from '@/store'
import { Fighter } from '@/data/characters'


export default Vue.extend({
    name: 'add-game-dialog',
    props: ['value'],
    data() {
        return {
            player1: null as Player | null,
            player2: null as Player | null,
            fighter1: null as Fighter | null,
            fighter2: null as Fighter | null,
            victor: null,
            twoStock: false,
            comeback: false,
            style1: 0,
            style2: 0
        }
    },
    computed: {
        showModal: {
            get(): boolean {
                return this.value
            },
            set(val: boolean) {
                this.$luigi()
                this.$emit('input', val)            }
        },
        validated() {
            if (this.player1 && this.player2 && this.fighter1 && this.fighter2 && this.victor) {
                return true
            } else {
                return false
            }
        }
    },
    watch: {
        player1(val) {
            if (val && !this.fighter1) this.fighter1 = val.mains[0]
            if (val && val === this.player2) this.player2 = null
        },
        player2(val) {
            if (val && !this.fighter2) this.fighter2 = val.mains[0]
            if (val && val === this.player1) this.player1 = null
        },
        twoStock(val) {
            if (val === true && this.comeback) this.comeback = false
        },
        comeback(val) {
            if (val === true && this.twoStock) this.twoStock = false
        }
    },
    methods: {
        submit() {
            this.$luigi()
            const player1Instance: PlayerInstance = {
                player: this.player1!,
                fighter: this.fighter1!,
                victory: this.victor === 1 ? true : false,
                ...(this.style1 && { style: this.style1 })
            }
            const player2Instance: PlayerInstance = {
                player: this.player2!,
                fighter: this.fighter2!,
                victory: this.victor === 2 ? true : false,
                ...(this.style2 && { style: this.style2 })
            }
            const victor = this.victor === 1 ? player1Instance : player2Instance
            if (this.comeback) { victor.comeback = true }
            if (this.twoStock) { victor.twoStock = true }

            const newGame: Game = {
                players: [player1Instance, player2Instance],
                timestamp: Date.now()
            }

            this.$store.commit('addNewGame', newGame)
            this.close()
        },
        close() {
            this.$luigi()
            this.player1 = null
            this.player2 = null
            this.fighter1 = null
            this.fighter2 = null
            this.victor = null
            this.twoStock = false
            this.comeback = false
            this.style1 = 0
            this.style2 = 0

            this.showModal = false
        }
    }
})
