import Vue from 'vue'
import './plugins/vuetify'
import App from './App.vue'
import router from './router'
import store from './store'
import { luigi } from '@/plugins/luigi'

Vue.config.productionTip = false

Vue.prototype.$luigi = luigi

new Vue({
  router,
  store,
  render: (h) => h(App),
}).$mount('#app');
