


































































import Vue from 'vue'
import { Player } from '@/store'

export default Vue.extend({
    name: 'add-player-dialog',
    props: ['value'],
    data() {
        return {
            name: '',
            tag: '',
            mains: [],
        }
    },
    computed: {
        showModal: {
            get(): boolean {
                return this.value
            },
            set(val: boolean) {
                this.$luigi()
                this.$emit('input', val)
            }
        },
        validated() {
            if (this.name && this.tag && this.mains && !this.tagTaken) return true
        },
        tagTaken() {
            return this.$store.state.players.some((player: Player) => this.tag === player.tag)
        }
    },
    methods: {
        submit() {
            const newPlayer = {
                name: this.name,
                tag: this.tag,
                mains: this.mains
            }

            this.$store.commit('addNewPlayer', newPlayer)
            this.close()
        },
        remove(item: any) {
            // @ts-ignore
            const index = this.mains.indexOf(item)
            if (index >= 0) this.mains.splice(index, 1)
        },
        close() {
            this.name = ''
            this.tag = ''
            this.mains = []

            this.showModal = false
        }
    }
})
