














import Vue from 'vue'
import AddPlayerDialog from '@/components/AddPlayerDialog.vue'
import AddGameDialog from '@/components/AddGameDialog.vue'

export default Vue.extend({
    name: 'tool-bar',
    props: ['value'],
    components: {
        AddPlayerDialog,
        AddGameDialog
    },
    data() {
        return {
            addPlayer: false,
            addGame: false
        }
    },
    computed: {
        drawer: {
            get(): boolean { return this.value },
            set(val: boolean) { this.$emit('input', val) }
        }
    }
})
